import React from "react"
import { MyBase } from "@macashipo/mlib"
import WrapControl from "./_wrapControl"
import useConfigFormControl from "../../hooks/useConfigFormControl"

import {
  FlowGetMoreOfKey,
  KMore,
  FlowGetMore,
} from "src/helpers/Flows/FormControl"
import styled from "styled-components"
import {
  getDateWithHourMinute,
  getDateWithString,
  getStringHourMinuteFromDate,
  getStringDateFromDate,
  isStringDate,
  isStringHourMinute,
} from "src/helpers/U"

const Wrap = styled.div`
  & > input {
    background: transparent;
    height: 36px;
  }
`
export default function MFCDatePicker(props) {
  const cfc = useConfigFormControl({ props })
  // console.warn("render MFCDatePicker:", props, cfc, cfc.valueControl)
  let _value =
    cfc.valueControl && typeof cfc.valueControl === "number"
      ? new Date(cfc.valueControl)
      : null
  let _more = FlowGetMore({ configFormControl: cfc.configFormControl })

  //check isText
  if (_more && _more[KMore.isText]) {
    if (_more[KMore.isOnlyTime] && isStringHourMinute(cfc.valueControl)) {
      _value = getDateWithHourMinute(cfc.valueControl)
    } else if (isStringDate(cfc.valueControl)) {
      _value = getDateWithString(cfc.valueControl)
    }
  }
  return (
    <WrapControl cfc={cfc} {...props}>
      <Wrap className="mfc-datepicker">
        <MyBase
          type="date_flatpickr"
          value={_value}
          onChange={v => {
            // console.warn("text:", v)
            if (v && v.length > 0) {
              let _date = v[0]
              let _valueUpdate = _date.getTime()
              // console.warn("update date:", _date)
              //check isText
              if (_more && _more[KMore.isText]) {
                if (_more[KMore.isOnlyTime]) {
                  _valueUpdate = getStringHourMinuteFromDate(_date)
                } else {
                  _valueUpdate = getStringDateFromDate(_date)
                }
              }
              cfc.updateValueControl(_valueUpdate)
            }
          }}
          attrs={{
            ...FlowGetMoreOfKey({
              configFormControl: cfc.configFormControl,
              key: KMore.flatpickr,
              defaultValue: {},
            }),
          }}
        />
      </Wrap>
    </WrapControl>
  )
}
