import DatePicker from "./datepicker"

export default function MFCDatePicker(props) {
  return (
    <DatePicker
      {...props}
      custom={{
        more: {
          isOnlyTime: true,
          flatpickr: {
            moreOptions: {
              enableTime: true,
              noCalendar: true,
              dateFormat: "H:i",
            },
          },
        },
      }}
    />
  )
}
